	module.exports = {"data":{"markdownRemark":{"html":"<style>\n.contents {\n  margin-left: 1.45rem;\n  margin-right: 1.45rem;\n  border-radius: 0.3em;\n  width: 60%;\n}\n</style>\n<p>HackerOne supports markdown syntax on reports, profiles, and security pages.</p>\n<div class=\"background contents\" markdown=\"1\">\n<table>\n<thead>\n<tr>\n<th>Learn to format these in markdown:</th>\n</tr>\n</thead>\n<tbody>\n<tr>\n<td><a href=\"#headers\">Headers</a></td>\n</tr>\n<tr>\n<td><a href=\"#blockquote\">Blockquote</a></td>\n</tr>\n<tr>\n<td><a href=\"#text\">Text Emphasis</a></td>\n</tr>\n<tr>\n<td><a href=\"#lists\">Lists</a></td>\n</tr>\n<tr>\n<td><a href=\"#links\">Links</a></td>\n</tr>\n<tr>\n<td><a href=\"#code\">Code</a></td>\n</tr>\n<tr>\n<td><a href=\"#user\">User Mentions</a></td>\n</tr>\n<tr>\n<td><a href=\"#report\">Report References</a></td>\n</tr>\n<tr>\n<td><a href=\"#attachment\">Attachment References</a></td>\n</tr>\n<tr>\n<td><a href=\"#inline\">Inline Images and Videos</a></td>\n</tr>\n</tbody>\n</table>\n</div>\n<h3 id=\"headers\">HEADERS</h3>\n<p>Input:</p>\n<pre><code>A First Level Header\n=====================\n\nA Second Level Header\n---------------------\n\n### Header 3\n</code></pre>\n<h1>A First Level Header</h1>\n<h2>A Second Level Header</h2>\n<h3>Header 3</h3>\n<h3 id=\"blockquote\">BLOCKQUOTE</h3>\n<pre><code>>text in blockquote\n>more text in blockquote\n</code></pre>\n<blockquote>\n<p>text in blockquote</p>\n</blockquote>\n<blockquote>\n<p>more text in blockquote</p>\n</blockquote>\n<h3 id=\"text\">TEXT EMPHASIS</h3>\n<p>Markdown Input:</p>\n<pre><code>*This text is italicized*\n_This text is underlined_\n**This text is bold**\n__This text is bold__\n~~This text is deleted~~\n</code></pre>\n<p>Output:\n\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/markdown-5-dedc49316b7817fa05b5f6dd690e3b5a-24537.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 500px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 28.371278458844134%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"text emphasis output\"\n        title=\"\"\n        src=\"/static/markdown-5-dedc49316b7817fa05b5f6dd690e3b5a-2f183.png\"\n        srcset=\"/static/markdown-5-dedc49316b7817fa05b5f6dd690e3b5a-67829.png 125w,\n/static/markdown-5-dedc49316b7817fa05b5f6dd690e3b5a-5c59f.png 250w,\n/static/markdown-5-dedc49316b7817fa05b5f6dd690e3b5a-2f183.png 500w,\n/static/markdown-5-dedc49316b7817fa05b5f6dd690e3b5a-c93e8.png 750w,\n/static/markdown-5-dedc49316b7817fa05b5f6dd690e3b5a-4e628.png 1000w,\n/static/markdown-5-dedc49316b7817fa05b5f6dd690e3b5a-24537.png 1142w\"\n        sizes=\"(max-width: 500px) 100vw, 500px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<h3 id=\"lists\">LISTS</h3>\n<h4>Unordered Lists</h4>\n<pre><code>* Candy.\n* Gum.\n* Juice.\n</code></pre>\n<p>this:</p>\n<pre><code>+ Candy.\n+ Gum.\n+ Juice.\n</code></pre>\n<p>and this:</p>\n<pre><code>- Candy\n- Gum.\n- Juice.\n</code></pre>\n<p>all produce the same output:</p>\n<ul>\n<li>Candy.</li>\n<li>Gum.</li>\n<li>Juice.</li>\n</ul>\n<h4>Numbered Lists</h4>\n<pre><code>1. Red\n2. Green\n3. Blue\n</code></pre>\n<ol>\n<li>Red</li>\n<li>Green</li>\n<li>Blue</li>\n</ol>\n<p>If you put blank lines between items, youâ€™ll get <code>&#x3C;p></code> tags for the list item text. You can create multi-paragraph list items by indenting the paragraphs by 4 spaces or 1 tab:</p>\n<pre><code>* A list item.\n\n  With multiple paragraphs.\n\n* Another item in the list.\n</code></pre>\n<ul>\n<li>\n<p>A list item.</p>\n<p>With multiple paragraphs.</p>\n</li>\n<li>\n<p>Another item in the list.</p>\n</li>\n</ul>\n<h3 id=\"links\">LINKS</h3>\n<p>Markdown supports two styles for creating links: <em>inline</em> and <em>reference</em>. With both styles, you use square brackets to delimit the text you want to turn into a link.</p>\n<p>Inline-style links use parentheses immediately after the link text. For example:</p>\n<pre><code>This is an [example link](http://example.com/).\n</code></pre>\n<p>This is an <a href=\"http://example.com/\">example link</a>.</p>\n<p>Optionally, you may include a title attribute in the parentheses. Hover on the link to see the title:</p>\n<pre><code>This is an [example link](http://example.com/ \"With a Title\").\n</code></pre>\n<p>This is an <a href=\"http://example.com/\" title=\"With a Title\">example link</a>.</p>\n<p>Reference-style links allow you to refer to your links by names, which you define elsewhere in your document:</p>\n<pre><code>I get 10 times more traffic from [Google][1] than from\n[Yahoo][2] or [MSN][3].\n\n[1]: http://google.com/        \"Google\"\n[2]: http://search.yahoo.com/  \"Yahoo Search\"\n[3]: http://search.msn.com/    \"MSN Search\"\n</code></pre>\n<p>I get 10 times more traffic from <a href=\"http://google.com/\">Google</a> than from <a href=\"http://search.yahoo.com/\">Yahoo</a> or <a href=\"http://search.msn.com/\">MSN</a>.</p>\n<p>The title attribute is optional. Link names may contain letters, numbers and spaces, but are not case sensitive:</p>\n<pre><code>I start my morning with a cup of coffee and\n[The New York Times][NY Times].\n\n[ny times]: http://www.nytimes.com/\n</code></pre>\n<p>I start my morning with a cup of coffee and <a href=\"http://www.nytimes.com/\">The New York Times</a>.</p>\n<h4>Email Links</h4>\n<p>You don't need to use markdown to create a <code>mailto:</code> link. Simply enter the email as is and it will automatically be converted to a <code>mailto:</code> link.</p>\n<h3 id=\"code\">CODE</h3>\nIn a regular paragraph, you can create code span by wrapping text in back tick quotes. Any ampersands (&) and angle brackets (< or >) will automatically be translated into HTML entities. This makes it easy to use Markdown to write about HTML example code.\n```\nThe text has sample `code`\n```\nThe text has sample code\n<p>To specify an entire block of pre-formatted code, wrap the text within 3 back tick quotes ```. Just like with code spans, &#x26;, &#x3C;, and > characters will be escaped automatically.</p>\n<pre><code>This is a multiline code block.\n</code></pre>\n<p>To use syntax highlighting, specify the content type after the three opening back tick quotes.</p>\n<pre><code>```javascript\ndocument.location = 'https://hackerone.com';\n</code></pre>\n<p>Go to the following web page to see which content types are available for syntax highlighting: <a href=\"https://github.com/jneen/rouge/tree/master/lib/rouge/demos\">https://github.com/jneen/rouge/tree/master/lib/rouge/demos</a>.</p>\n<h3 id=\"user\">USER MENTIONS</h3>\nYou can mention a user by prefixing username with '@' symbol\n<pre><code>@demo-member reported the issue\n</code></pre>\n<p>@demo-member reported the issue</p>\n<br>\n<h3 id=\"report\">REPORT REFERENCES</h3>\n<p>You can reference a report by prefixing report id with '#' symbol</p>\n<pre><code>#105887 is a publicly disclosed bug\n</code></pre>\n<p><a href=\"https://hackerone.com/reports/105887\">#105887</a> is a publicly disclosed bug</p>\n<h3 id=\"attachment\">ATTACHMENT REFERENCES</h3>\n<p>You can reference an attachment while writing reports, comments in reports and report summary. You can do this by writing 'F' followed by attachment id (F). The attachment id is displayed before the attachment name once the upload is successful.</p>\n<p>Example: Consider a user is creating a report and uploads an attachment. Once the attachment is uploaded successfully, you will see see the reference id with the attachment name.\n\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/markdown-1-ed165b333bbf0620a4009bda0d32f047-e222e.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 500px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 55.984919886899156%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"markdown-1\"\n        title=\"\"\n        src=\"/static/markdown-1-ed165b333bbf0620a4009bda0d32f047-2f183.png\"\n        srcset=\"/static/markdown-1-ed165b333bbf0620a4009bda0d32f047-67829.png 125w,\n/static/markdown-1-ed165b333bbf0620a4009bda0d32f047-5c59f.png 250w,\n/static/markdown-1-ed165b333bbf0620a4009bda0d32f047-2f183.png 500w,\n/static/markdown-1-ed165b333bbf0620a4009bda0d32f047-c93e8.png 750w,\n/static/markdown-1-ed165b333bbf0620a4009bda0d32f047-4e628.png 1000w,\n/static/markdown-1-ed165b333bbf0620a4009bda0d32f047-e222e.png 1061w\"\n        sizes=\"(max-width: 500px) 100vw, 500px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<p>Now you can reference the attachment in the report by writing 'F1' and the attachment is referenced in the report as shown below.\n\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/markdown-2-71108921d4ae55ba604da9b95b844b96-57d72.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 500px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 43.59214218896165%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"markdown-2\"\n        title=\"\"\n        src=\"/static/markdown-2-71108921d4ae55ba604da9b95b844b96-2f183.png\"\n        srcset=\"/static/markdown-2-71108921d4ae55ba604da9b95b844b96-67829.png 125w,\n/static/markdown-2-71108921d4ae55ba604da9b95b844b96-5c59f.png 250w,\n/static/markdown-2-71108921d4ae55ba604da9b95b844b96-2f183.png 500w,\n/static/markdown-2-71108921d4ae55ba604da9b95b844b96-c93e8.png 750w,\n/static/markdown-2-71108921d4ae55ba604da9b95b844b96-4e628.png 1000w,\n/static/markdown-2-71108921d4ae55ba604da9b95b844b96-57d72.png 1069w\"\n        sizes=\"(max-width: 500px) 100vw, 500px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<p>When the link is clicked, the attachment is displayed in the modal.\n\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/markdown-3-66a33e690be39b401e0df94dde3c9e24-aba2f.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 500px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 110.78524124881741%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"markdown-3\"\n        title=\"\"\n        src=\"/static/markdown-3-66a33e690be39b401e0df94dde3c9e24-2f183.png\"\n        srcset=\"/static/markdown-3-66a33e690be39b401e0df94dde3c9e24-67829.png 125w,\n/static/markdown-3-66a33e690be39b401e0df94dde3c9e24-5c59f.png 250w,\n/static/markdown-3-66a33e690be39b401e0df94dde3c9e24-2f183.png 500w,\n/static/markdown-3-66a33e690be39b401e0df94dde3c9e24-c93e8.png 750w,\n/static/markdown-3-66a33e690be39b401e0df94dde3c9e24-4e628.png 1000w,\n/static/markdown-3-66a33e690be39b401e0df94dde3c9e24-aba2f.png 1057w\"\n        sizes=\"(max-width: 500px) 100vw, 500px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<h3 id=\"inline\">INLINE IMAGES AND VIDEOS</h3>\nYou can inline images and videos in the report description, comments and report summary by writing the attachment reference id within curly braces (as in {F:id}). Example: For the above attachment, the markdown\n```\n Here is an inlined image of an apple {F1}\n```\nwill render the following:\n![markdown-4](./images/markdown-4.png)","frontmatter":{"path":"/programs/using-markdown.html","id":"programs/using-markdown","title":"Using Markdown","description":null}}},"pathContext":{}}

